<template>
  <div class="Life">
    <transition-group name="setLife">
    <div class="Life-boat" v-if="boat41" key="boat41">
      <div class="Life-boat__i">
      </div><div class="Life-boat__i">
      </div><div class="Life-boat__i">
      </div><div class="Life-boat__i">
      </div>
    </div><div class="Life-boat" v-if="boat31" key="boat31">
      <div class="Life-boat__i">
      </div><div class="Life-boat__i">
      </div><div class="Life-boat__i">
      </div>
    </div><div class="Life-boat" v-if="boat32" key="boat32">
      <div class="Life-boat__i">
      </div><div class="Life-boat__i">
      </div><div class="Life-boat__i">
      </div>
    </div><div class="Life-boat" v-if="boat21" key="boat21">
      <div class="Life-boat__i">
      </div><div class="Life-boat__i">
      </div>
    </div><div class="Life-boat" v-if="boat22" key="boat22">
      <div class="Life-boat__i">
      </div><div class="Life-boat__i">
      </div>
    </div><div class="Life-boat" v-if="boat23" key="boat23">
      <div class="Life-boat__i">
      </div><div class="Life-boat__i">
      </div>
    </div><div class="Life-boat" v-if="boat11" key="boat11">
      <div class="Life-boat__i"></div>
    </div><div class="Life-boat" v-if="boat12" key="boat12">
      <div class="Life-boat__i"></div>
    </div><div class="Life-boat" v-if="boat13" key="boat13">
      <div class="Life-boat__i"></div>
    </div><div class="Life-boat" v-if="boat14" key="boat14">
      <div class="Life-boat__i"></div>
    </div><div class="Life-boatdead" v-if="!boat41" key="boat41no">
      <div class="Life-boat__i">
      </div><div class="Life-boat__i">
      </div><div class="Life-boat__i">
      </div><div class="Life-boat__i">
      </div>
    </div><div class="Life-boatdead" v-if="!boat31" key="boat31no">
      <div class="Life-boat__i">
      </div><div class="Life-boat__i">
    </div><div class="Life-boat__i">
    </div>
    </div><div class="Life-boatdead" v-if="!boat32" key="boat32no">
      <div class="Life-boat__i">
      </div><div class="Life-boat__i">
    </div><div class="Life-boat__i">
    </div>
    </div><div class="Life-boatdead" v-if="!boat21" key="boat21no">
      <div class="Life-boat__i">
      </div><div class="Life-boat__i">
    </div>
    </div><div class="Life-boatdead" v-if="!boat22" key="boat22no">
      <div class="Life-boat__i">
      </div><div class="Life-boat__i">
    </div>
    </div><div class="Life-boatdead" v-if="!boat23" key="boat23no">
      <div class="Life-boat__i">
      </div><div class="Life-boat__i">
    </div>
    </div><div class="Life-boatdead" v-if="!boat11" key="boat11no">
      <div class="Life-boat__i"></div>
    </div><div class="Life-boatdead" v-if="!boat12" key="boat12no">
      <div class="Life-boat__i"></div>
    </div><div class="Life-boatdead" v-if="!boat13" key="boat13no">
      <div class="Life-boat__i"></div>
    </div><div class="Life-boatdead" v-if="!boat14" key="boat14no">
      <div class="Life-boat__i"></div>
    </div>
    </transition-group>
  </div>
</template>

<script>
export default {
  name: 'Life',
  props: ['player'],
  computed: {
    boat: function () {
      return this.$store.state.game.players[this.$store.state.players[this.player]].boat.n
    },
    boat41: function () {
      return (this.boat['4'][1] < 1)
    },
    boat31: function () {
      return (this.boat['3'][1] < 1)
    },
    boat32: function () {
      return (this.boat['3'][1] < 2)
    },
    boat21: function () {
      return (this.boat['2'][1] < 1)
    },
    boat22: function () {
      return (this.boat['2'][1] < 2)
    },
    boat23: function () {
      return (this.boat['2'][1] < 3)
    },
    boat11: function () {
      return (this.boat['1'][1] < 1)
    },
    boat12: function () {
      return (this.boat['1'][1] < 2)
    },
    boat13: function () {
      return (this.boat['1'][1] < 3)
    },
    boat14: function () {
      return (this.boat['1'][1] < 4)
    }
  }
}
</script>

<style lang="sass">
  .Life
    white-space: nowrap;

  .Life-boat, .Life-boatdead
    display: inline-block;
    vertical-align: top;
    height: 4px;
    margin: 0 3px 0 0;
    transition: all .3s ease;

  .Life-boat__i
    display: inline-block;
    vertical-align: top;
    width: 4px;
    height: 4px;
    background: $blue;
    margin: 0 1px 0 0;

  .Life-boatdead .Life-boat__i
    background: #ccc;

  .setLife-enter-active
    z-index: 2;

  .setLife-leave-active
    z-index: 3;

  .setLife-leave-active .Life-boat__i
    background: #000 !important;

  .setLife-enter, .setLife-leave-to
    opacity: 0;
    width: 0;
    margin: 0;

  .setLife-enter
    transform: translateX(-20px) scale(3);

  .setLife-leave-to
    transform: translateX(20px) scale(3);

</style>



