import Vue from 'vue'
import Vuex from 'vuex'
import Vuels from 'vue-ls'
import { polyfill } from 'es6-promise'
import VueClipboard from 'vue-clipboard2'
import Push from 'push.js'
import clone from 'clone'

polyfill()

Vue.use(Vuex)
Vue.use(Vuels, { namespace: 'vue__' })
Vue.use(VueClipboard)

const utils = require('./utils')
let user = utils.user
let game = utils.game
let players = utils.players
let window = utils.window
let putTimer = false

const store = new Vuex.Store({
  state: {
    io: {},
    boardi: utils.boardi,
    user: user,
    game: game,
    players: players,
    currentBoard: 0,
    window: window,
    barMenuOpened: false,
    text: '',
    tab: true,
    console: 'game: ' + game._id + '\r\nv: 0.8.0\r\n'
  },
  getters: {
  },
  mutations: {
    setTab: (state, tab) => {
      state.tab = tab
    },
    setSocket: (state, socket) => {
      state.io = socket
      socket.emit('register', game._id)
    },
    SOCKET_CONNECT (state) {
      state.io.emit('register', game._id)
      if (!putTimer) {
        this.dispatch('getGame')
      }
      state.console = 'socket connect\r\n' + state.console
    },
    SOCKET_CONNECT_ERROR (state) {
      state.console = 'socket error\r\n' + state.console
    },
    SOCKET_UPDATE_USER (state, items) {
      let item = items.pop()
      user.version = item.version
      if (item._id === user._id) {
        user.name = item.name
        this.commit('setUser')
        state.console = 'user: ' + item.name + '\r\n' + state.console
      }
    },
    SOCKET_UPDATE_GAME_USER (state, items) {
      let item = items.pop()
      game.version = item.version
      game.players[item._id].user.name = item.name
      this.commit('setGame')
      if (item._id !== user._id) {
        state.console = 'Opponent rename: ' + item.name + '\r\n' + state.console
      }
    },
    SOCKET_JOIN_GAME (state, items) {
      let item = items.pop()
      let playerKeys = Object.keys(JSON.parse(item.players))
      playerKeys.splice(playerKeys.indexOf(user._id), 1)
      let secondplayer = playerKeys.shift()
      this.commit('setSecondPlayer', secondplayer)
      if (game.players[0]) {
        delete game.players[0]
      }
      state.console = 'joined\r\n' + state.console
      this.commit('updateGame', item)
    },
    SOCKET_UPDATE_GAME (state, items) {
      let item = items.pop()
      this.commit('updateGame', item)
    },
    updateGame (state, item) {
      game.turn = item.turn
      game.version = item.version
      game.players = JSON.parse(item.players)
      let second = game.players[state.players[1]]
      if (second && second.board) {
        if ((game.players[state.players[0]].phase === 'end') || (game.players[state.players[1]].phase === 'end')) {
          if (item.newgame) { // end of game
            game.newgame = item.newgame
          }
          if (game.players[state.players[0]].phase === 'end') {
            this.dispatch('endGame', 0)
          } else if (game.players[state.players[1]].phase === 'end') {
            this.dispatch('endGame', 1)
          }
        } else {
          if ((game.players[state.players[0]].phase === 'ready') && (game.players[state.players[1]].phase === 'ready')) {
            if (item.lastid) {
              let last = game.players[item.lastid].board[0][item.lastindex[0]][item.lastindex[1]]
              last.class += ' last'
              let secondplayer = 1 - players.indexOf(item.lastid)
              state.console = game.players[players[secondplayer]].user.name + ': ' + utils.boardi.col[item.lastindex[1]] + utils.boardi.row[item.lastindex[0]] + '\r\n' + state.console
            }
            if ((game.turn === user._id) && !state.tab) {
              this.dispatch('notify', user.name + ', your turn!')
            }
          }
          second.board[0] = false
        }
      }
      this.commit('setGame')
      state.console = 'upd: ' + game.version + '\r\n' + state.console
      if (item.event) {
        if (item.eventuser !== user._id) {
          state.console = game.players[item.eventuser].user.name + ': ' + item.event + '\r\n' + state.console
        }
      }
    },
    set (state, { type, items }) {
      state[type] = items
    },
    setUser (state) {
      state.user = user
      Vue.ls.set('user', user)
    },
    setSecondPlayer (state, secondplayerid) {
      state.players[1] = secondplayerid
    },
    setGame (state) {
      state.game = game
      Vue.ls.set('game', game)
    },
    setText (state, text) {
      state.text = text
    },
    barMenuOpen (state, isOpened) {
      state.barMenuOpened = (isOpened !== 'toggle') ? isOpened : !state.barMenuOpened
    }
  },
  actions: {
    notify (context, text) {
      Push.create(text, {
        // body: 'Hows it hangin?',
        icon: '/static/apple-touch-icon.png',
        timeout: 10000,
        onClick: function () {
          window.focus()
          this.close()
        }
      })
    },
    setPutTimer (context, {method, tosend}) {
      context.state.io.emit('register', game._id)
      game.version--
      clearTimeout(putTimer)
      let putTimerVer = game.version
      if (context.state.io.connected) {
        context.state.io.emit(method, tosend)
      } else {
        putTimer = setTimeout(function () {
          if (putTimerVer === game.version) {
            context.state.console = 'timer: ' + game.version + '\r\n' + context.state.console
            context.dispatch('setPutTimer', {method, tosend})
          } else {
            clearTimeout(putTimer)
            putTimer = false
          }
        }, 5000)
      }
    },
    newGame (context, id = false) {
      context.commit('setText', '')
      context.commit('setSecondPlayer', 0)
      context.dispatch('selectBoard', 0)
      game = utils.newGame()
      if (id) {
        game._id = id
        context.commit('setGame')
        context.dispatch('saveOrJoinGame')
      } else {
        context.dispatch('saveGame')
      }
      context.state.console = 'new game: ' + game._id + '\r\n' + context.state.console
      return true
    },
    setPlayer ({commit, dispatch}, {type, items}) {
      user[type] = items
      commit('setUser')
      let tosend = {
        _id: game._id,
        user: user
      }
      dispatch('setPutTimer', {method: 'saveUser', tosend})
    },
    disposition ({commit, dispatch}, index) {
      let cell = game.players[players[0]].board[0][index[0]][index[1]].class
      if (cell === 'none') {
        utils.setCell(index)
      } else if (cell === 'boat') {
        utils.delCell(index)
      }
      dispatch('saveGame')
    },
    autoDisposition ({dispatch}) {
      utils.autoDisposition()
      dispatch('saveGame')
    },
    saveGame (context, event = false) {
      context.commit('setGame')
      let tosend = clone(game)
      if (event) {
        tosend.event = event
      }
      context.dispatch('setPutTimer', {method: 'save', tosend})
    },
    saveOrJoinGame (context) {
      let tosend = clone(game)
      context.dispatch('setPutTimer', {method: 'join', tosend})
      context.state.console = 'join\r\n' + context.state.console
    },
    joinGame (context, gameId) {
      game._id = gameId
      context.commit('setText', '')
      context.commit('setSecondPlayer', 0)
      context.dispatch('selectBoard', 0)
      let tosend = {
        _id: gameId,
        userid: user._id
      }
      context.dispatch('setPutTimer', {method: 'join', tosend})
      context.state.console = 'join\r\n' + context.state.console
    },
    finishDisposition ({commit, dispatch}) {
      utils.finishDisposition()
      game.players[user._id].phase = 'ready'
      game.players[user._id].boat.n = utils.boatinit.n
      game.players[user._id].boat.c = 100
      game.players[user._id].boat.ok = utils.boatinit.ok
      dispatch('saveGame', 'ready')
    },
    endGame ({commit}, n) {
      n = 1 - n
      game.players[players[n]].board[1] = game.players[players[n]].board[0]
      game.version++
      commit('setGame')
      commit('setText', game.players[players[n]].user.name + ' won!')
    },
    clearDisposition ({dispatch}) {
      utils.clearDisposition()
      game.players[user._id].boat = utils.boatinit
      dispatch('saveGame')
    },
    getGame (context) {
      if (game.version > 0) {
        let tosend = {
          _id: game._id,
          userid: user._id
        }
        context.dispatch('setPutTimer', {method: 'get', tosend})
        context.state.console = 'get\r\n' + context.state.console
      }
    },
    selectBoard ({commit}, i) {
      commit('set', {
        type: 'currentBoard',
        items: +i
      })
    },
    barMenuOpen ({commit}, isOpened = 'toggle') {
      commit('barMenuOpen', isOpened)
    },
    move (context, index) {
      if (game.turn === user._id) {
        let cell = game.players[players[1]].board[1][index[0]][index[1]]
        if (cell.class === 'none') {
          game.turn += 'moves'
          cell.class = 'loading'
          let tosend = {
            _id: game._id,
            userid: user._id,
            secondid: players[1],
            index: index
          }
          context.dispatch('setPutTimer', {method: 'move', tosend})
          context.state.console = 'move: ' + utils.boardi.col[index[1]] + utils.boardi.row[index[0]] + '\r\n' + context.state.console
        }
      }
    },
    windowResized ({commit}) {
      window.width = document.documentElement.clientWidth
      window.height = document.documentElement.clientHeight
      window.land = (window.width > window.height) ? 1 : 0
      window.boardWidth = (window.land) ? window.height : window.width
      if (window.boardWidth > window.boardMaxWidth) window.boardWidth = window.boardMaxWidth
      window.cellWidth = window.boardWidth / 11
      if (window.width > window.boardWidth * 2 + 20) window.land = 2
      window.barHeight = document.getElementById('Bar').offsetHeight
      window.mainHeight = window.height - window.barHeight
      commit('set', { type: 'window', items: window })
    }
  }
})

export default store



