import Vue from 'vue'
import Router from 'vue-router'
import Basic from '@/components/Basic'
import User from '@/components/User'
import Game from '@/components/Game'
import Rules from '@/components/Rules'
import About from '@/components/About'
import Console from '@/components/Console'
import VueHead from 'vue-head'

Vue.use(Router)
Vue.use(VueHead)

const router = new Router({
  mode: 'hash',
  routes: [
    {
      path: '/',
      name: 'Basic',
      component: Basic
    },
    {
      path: '/g/:gameId',
      name: 'Join Game',
      component: Game
    },
    {
      path: '/user',
      name: 'Captain',
      component: User
    },
    {
      path: '/game',
      name: 'Game',
      component: Game
    },
    {
      path: '/rules',
      name: 'Rules',
      component: Rules
    },
    {
      path: '/about',
      name: 'About',
      component: About
    },
    {
      path: '/console',
      name: 'Console',
      component: Console
    }
  ]
})

export default router



